import { useTranslation } from "react-i18next";
import { assestsNavbarUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../../../FinanceManager/FinanceManager.module.css";
import { useCallback, useEffect, useState } from "react";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../../Utils/Functions/debounce";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import Loading from "../../../../Loader/Loading";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import { useStoreSevenIssue } from "../../../../Hooks/ApiHooks/StoreManager/store-manager-api";
import StoreSevenIssueCard from "./StoreSevenIssueCard";

const StoreSevenIssue = ({ flow, mid }) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const { storeSevenIssue, storeSevenIssueLoading } = useStoreSevenIssue({
    data: {
      mid: mid,
      page: page,
      limit: 10,
      search: search,
      flow: flow,
    },
    skip: flow ? !mid : !flow,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (storeSevenIssue?.list?.length) {
      let obj = nextPage(storeSevenIssue?.list?.length);
      setState(() => obj);
    }
  }, [storeSevenIssue?.list?.length]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };
  return (
    <>
      <div
        className={style.fm_search_full}
        style={{
          marginBottom: "0.5rem",
        }}
      >
        <section
          className={style.fm_search_full_container}
          style={{
            gap: "1rem",
          }}
        >
          <div
            className={style.fm_search_full_container_inner}
            style={{
              flex: "1.5 1.5",
            }}
          >
            <img
              className={style.fm_search_full_icon}
              alt="search icon"
              src={`${assestsNavbarUrl}/navbar-search.svg`}
            />
            <input
              type="text"
              placeholder={t("search")}
              onChange={onSearchEvent}
            />
          </div>
        </section>
      </div>

      {storeSevenIssue?.list?.map((issue, index) =>
        storeSevenIssue?.list?.length === index + 1 ? (
          <div key={issue?._id} ref={ref}>
            <StoreSevenIssueCard issue={issue} flow={flow} />
          </div>
        ) : (
          <StoreSevenIssueCard issue={issue} key={issue?._id} flow={flow} />
        )
      )}
      {storeSevenIssueLoading ? (
        search ? (
          <QvipleLoader />
        ) : (
          <Loading />
        )
      ) : search ? (
        storeSevenIssue?.list?.length === 0 && (
          <EmptyMessage
            title="No issue goods found related this search."
            customStyleContainer={{
              marginTop: "1.5rem",
            }}
          />
        )
      ) : (
        storeSevenIssue?.list?.length === 0 && (
          <EmptyMessage
            title="No issue goods."
            customStyleContainer={{
              marginTop: "1.5rem",
            }}
          />
        )
      )}
    </>
  );
};

export default StoreSevenIssue;
