import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { existingRequiredField } from "../../../../Validation/functions/checkingRequiredField";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../../FinanceManager/Income/Income.module.css";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import CreateButton from "../../../../Ui/Button/CreateButton";
import FormInput from "../../../../Ui/Input/FormInput";

const IssueRecordForm = ({ setState, state, setStep, onClose }) => {
  const { t } = useTranslation();
  const [goods, setGoods] = useState([]);
  const [errorField, setErrorField] = useState("");
  const [messageField, setMessageField] = useState("");

  useEffect(() => {
    if (state?.goodsId) {
      let rr = [];
      for (let st of state?.goods) {
        rr.push({
          goodId: st?._id ?? "",
          name: st?.goods_name ?? "",
          volume: st?.goods_volume ?? "",
          actual_volume: st?.goods_volume ?? "",
        });
      }
      setGoods(rr);
    }
  }, [state?.goodsId]);

  const onNext = () => {
    let validation = {};
    for (let st of goods) {
      if (st?.volume) {
      } else {
        validation[st.name] = "* required";
      }
    }
    if (existingRequiredField(validation)) {
      setState((prev) => ({
        ...prev,
        arr: goods,
      }));
      setStep(5);
    } else {
      setErrorField(validation);
    }
  };

  const onChange = (e, ind) => {
    let ipn = [...goods];
    if (+e.target.value >= +ipn[ind].actual_volume) {
      setMessageField({
        [ipn[ind]
          .name]: `Quantity should be less than or equal to remaining quantity - ${ipn[ind].actual_volume}`,
      });
    } else {
      ipn[ind].volume = +e.target.value;
      setGoods(() => ipn);
    }
  };
  return (
    <Modal onClose={onClose}>
      <div className={style.modal_container}>
        <UserMemberContentWrapper>
          <div className={style.modal_container_outer_header}>
            <div className={style.modal_container_header}>
              <h6>{t("goods_quantity")}</h6>
            </div>

            <img
              src={`${assestsUserFinanceUrl}/close.svg`}
              alt="close icon"
              onClick={onClose}
            />
          </div>
        </UserMemberContentWrapper>
        <BorderBottom
          customStyleBorder={{
            marginBottom: "0",
          }}
        />
        <UserMemberContentWrapper
          customStyle={{
            paddingBottom: "1rem",
          }}
        >
          {goods?.map((gd, index) => (
            <FormInput
              label={gd?.name}
              placeholder={t("goods_quantity_placeholder")}
              name={gd?.name}
              value={gd?.volume}
              type={"text"}
              onChange={(e) => onChange(e, index)}
              errorShow={errorField[gd?.name]}
              key={gd?.goodId}
              msg={messageField[gd?.name]}
            />
          ))}

          <CreateButton label="confirm" onAction={onNext} />
        </UserMemberContentWrapper>
      </div>
    </Modal>
  );
};

export default IssueRecordForm;
