import { useTranslation } from "react-i18next";
import { assestsNavbarUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../../../FinanceManager/FinanceManager.module.css";
import { useCallback, useEffect, useState } from "react";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../../Utils/Functions/debounce";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import Loading from "../../../../Loader/Loading";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import { useStoreSevenConsumption } from "../../../../Hooks/ApiHooks/StoreManager/store-manager-api";

const StoreSevenConsumption = ({ flow, mid }) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const { storeSevenConsumption, storeSevenConsumptionLoading } =
    useStoreSevenConsumption({
      data: {
        mid: mid,
        page: page,
        limit: 10,
        search: search,
        flow: flow,
      },
      skip: flow ? !mid : !flow,
    });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (storeSevenConsumption?.list?.length) {
      let obj = nextPage(storeSevenConsumption?.list?.length);
      setState(() => obj);
    }
  }, [storeSevenConsumption?.list?.length]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };
  return (
    <>
      <div
        className={style.fm_search_full}
        style={{
          marginBottom: "0.5rem",
        }}
      >
        <section
          className={style.fm_search_full_container}
          style={{
            gap: "1rem",
          }}
        >
          <div
            className={style.fm_search_full_container_inner}
            style={{
              flex: "1.5 1.5",
            }}
          >
            <img
              className={style.fm_search_full_icon}
              alt="search icon"
              src={`${assestsNavbarUrl}/navbar-search.svg`}
            />
            <input
              type="text"
              placeholder={t("search")}
              onChange={onSearchEvent}
            />
          </div>
        </section>
      </div>

      {/* {storeSevenConsumption?.list?.map((goods, index) =>
        storeSevenConsumption?.list?.length === index + 1 ? (
          <div key={goods?._id} ref={ref}>
            <StoreIssueGoodsCard goods={goods} />
          </div>
        ) : (
          <StoreIssueGoodsCard goods={goods} key={goods?._id} />
        )
      )} */}
      {storeSevenConsumptionLoading ? (
        search ? (
          <QvipleLoader />
        ) : (
          <Loading />
        )
      ) : search ? (
        storeSevenConsumption?.list?.length === 0 && (
          <EmptyMessage
            title="No consumption goods found related this search."
            customStyleContainer={{
              marginTop: "1.5rem",
            }}
          />
        )
      ) : (
        storeSevenConsumption?.list?.length === 0 && (
          <EmptyMessage
            title="No consumption goods."
            customStyleContainer={{
              marginTop: "1.5rem",
            }}
          />
        )
      )}
    </>
  );
};

export default StoreSevenConsumption;
